export default {
  data() {
    return {
      post: {
        id:null,
        title: null,
        subTitle: null,
        author: null,
        text: null,
      },
    };
  },
  props: {
    isUpdate: {
      type: Boolean,
      required: false,
      default: false,
    },
    post: {
      type: Object,
      required: false,
    },
  },
  methods: {
    onSubmit() {
      this.$emit("submit", this.post);
    },
  },
  created() {
    this.post = this.post
      ? this.post
      : {
          id: null,
          title: null,
          subTitle: null,
          text: null,
          author: null,
        };
  },
};
